body {
  margin: 0;
  padding: 0;
  background: #ddd;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 852px;
  height: 60px;
  background-color: #f5f5f5;
  padding: 10px;
  box-sizing: border-box;
  position: relative;
}

.input-group {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 16px;
}

.input-group input {
  width: 50px;
  text-align: center;
  font-size: 16px;
  padding: 2px;
  -moz-appearance: textfield;
}

.input-group input::-webkit-inner-spin-button,
.input-group input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.submit-button {
  position: absolute;
  right: 10px;
  font-size: 16px;
  padding: 5px 10px;
}

.pause-button {
  position: absolute;
  right: 100px;
  font-size: 16px;
  padding: 5px 10px;
}

/* Background image for the animation container */
.animation-container {
  width: 852px;
  height: 393px;
  position: relative;
  overflow-x: scroll !important;
  overflow-y: hidden;
  background-image: url('/public/dragonfly-bg.png');
  background-repeat: repeat-x;
  background-position: center bottom;
  background-size: auto 100%;
  animation: natureAnimation 30s linear infinite;
  cursor:pointer;
}

/* When paused, you may want to pause the background animation: */
.animation-container.paused {
  animation-play-state: paused;
  overflow-x: scroll !important;
  overflow-y: hidden;
}

/* Keyframes for background translation */
@keyframes natureAnimation {
  from {
    background-position: 0 bottom;
  }
  to {
    background-position: -200px bottom;
  }
}
.input-top {
  border: 2px solid peru;
  border-radius: 4px;
  padding: 4px;
}

/* Style for the bottom input to match the bottom trail color (e.g., "olivedrab" or "forestgreen") */
.input-bottom {
  border: 2px solid forestgreen;
  border-radius: 4px;
  padding: 4px;
}

/* Optionally, remove default focus outline and add your own */
.input-top:focus,
.input-bottom:focus {
  outline: none;
  box-shadow: 0 0 5px rgba(0,0,0,0.3);
}